import React, {useState} from 'react'
import './portfolio.scss'
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRightTwoTone';
import {languages} from '../Languages/language-info'
import { websiteInfo } from './portfolio-info';

const Portfolio = () => {
const [count, setCount] = useState(0)
const [imgAppear, setImgAppear] = useState('')
const [animComplete, setAnimComplete] = useState(true)
const [highlight, setHighlight] = useState(null)

const incrementCount = () => {
if (animComplete) {
  setAnimComplete(false)
  setCount((prev) => prev === 0 ? websiteInfo.length - 1 : prev - 1)
setImgAppear('img-appear')
setTimeout(() => {
setImgAppear('')
setAnimComplete(true)
},410)}
}

const decerementCount = () => {
  if (animComplete) {
    setAnimComplete(false)
setCount((prev) => prev === websiteInfo.length - 1 ? 0 : prev + 1)
setImgAppear('img-appear')
setTimeout(() => {
setImgAppear('')
setAnimComplete(true)
},410)}
}

const highlightPortImg = () => {
setHighlight({outline: "3px solid #74C991", outlineOffset: -3, transition: "0.4s" })
}

const removeHighlightPortImg = () => {
  setHighlight(null)
  }

  return (
    <div id="portfolio">
      <div className='port-img-div'>
        <ChevronLeftIcon className='arrow left' onClick={decerementCount} />
        <a href={websiteInfo[count].url}><img onMouseOver={highlightPortImg} onMouseLeave={removeHighlightPortImg} className={`port-img-bg`} src={websiteInfo[count].img} alt={websiteInfo[count].name} /><img style={highlight} onMouseOver={highlightPortImg} onMouseLeave={removeHighlightPortImg} className={`port-img ${imgAppear}`} src={websiteInfo[count].img} alt={websiteInfo[count].name} /></a>
        <ChevronRightIcon className='arrow right' onClick={incrementCount} />

      </div>
      <h3>{websiteInfo[count].name}</h3>
      
     <div className='made-with'>
     <p>Made With</p>
     <div className='port-tools'>{Object.values(languages)
    .filter((lang) => websiteInfo[count].tools.includes(lang.name)).map((item, i) => <img src={item.url} key={i} alt={item.name} draggable="false"/>)}
     </div></div>
     </div>
  )
}

export default Portfolio
